.product{
    width: 300px;
    height: 510pox;
    border: 1px solid #95A0A7;
    border-radius: 8px;
    padding: 7px;
    position: relative;
}
.product img{
    width: 286px;
    height: 286px;
    border-radius: 8px;
    margin: 7px;
}
.product-name{
    font-family: 'Lato';
    font-style: normal;
    font-weight: 400;
    font-size: 21px;
    line-height: 25px;
    margin-top: 7px;
}
.product-info{
    margin: 0 14px;
    margin-bottom: 50px;
}

.btn-cart{
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    background-color:#FFE0B3;
    border: none;
    border-radius: 0px 0px 8px 8px;
    position: absolute;
    bottom: 0;
    margin-left: -7px;
    gap: 4px;
}
.btn-cart:hover{
    background-color: orange;
}
